// Ajax request ES6 function
export default obj => {
    return new Promise((resolve, reject) => {
        let xhr = new XMLHttpRequest();
        xhr.open(obj.method || "GET", obj.url);
        if (obj.headers) {
            Object.keys(obj.headers).forEach(key => {
                xhr.setRequestHeader(key, obj.headers[key]);
            });
        }
        xhr.onload = () => {
            if (xhr.status >= 200 && xhr.status < 300) {
                resolve(xhr.response);
            } else {
                reject(xhr);
            }
        };
        xhr.onerror = () => reject(xhr);
        // Custom functions for loader
        if (obj.onProgressFunction) {
            // Custom loader
            xhr.onprogress = () => {
                obj.onProgressFunction(xhr); // call .apply(args...) with arguments

            };
        } else if (obj.onLoadStartFunction) {
            xhr.onloadstart = () => {
                obj.onLoadStartFunction(xhr); // call .apply(args...) with arguments
            };
        } else if (obj.onLoadEndFunction) {
            xhr.onloadend = () => {
                obj.onLoadEndFunction(xhr); // call .apply(args...) with arguments
            };
        }
        // Send request
        xhr.send(obj.body);
    });
};